<template>
  <div class="map">
    <div class="amap-wrapper">
      <!-- <el-amap class="amap-box" :vid="'amap-vue'"></el-amap> -->
      <el-amap ref="map" vid="amapDemo" :amap-manager="amapManager" :center="center" :zoom="zoom" :plugin="plugin" :events="events" class="amap-demo">
        <el-amap-marker v-for="(marker, index) in markers" :key="index" :position="marker.position" :events="marker.events" :visible="marker.visible" :draggable="marker.draggable" :vid="index" :icon="icon"></el-amap-marker>
      </el-amap>

    </div>
  </div>
</template>

<script>
import { amapManager } from 'vue-amap'
import icon from '../assets/images/like.png'
export default {
  name: 'maps',
  data () {
    return {
      icon,
      amapManager,
      zoom: 5,
      center: [116.42, 39.93],
      events: {
        init: (o) => {
          console.log(o.getCenter())
          console.log(this.$refs.map.$$getInstance())
          o.getCity(result => {
            console.log(result)
          })
        },
        'moveend': () => {
        },
        'zoomchange': () => {
        },
        'click': (e) => {
          alert('map clicked')
        }
      },
      plugin: ['ToolBar', {
        pName: 'MapType',
        defaultType: 0,
        events: {
          init (o) {
            console.log(o)
          }
        }
      }],
      markers: [
        {
          position: [116.405467, 39.907761],
          events: {
            click: () => {
              alert('click marker')
            },
            dragend: (e) => {
              console.log('---event---: dragend')
              this.markers[0].position = [e.lnglat.lng, e.lnglat.lat]
            }
          }
        },
        {
          position: [120.13, 30.27]
        }
      ]
    }
  },
  methods: {
  }
}
</script>

<style lang="scss" scoped>
@import '../assets/scss/variable.scss';

.map {
  .amap-wrapper {
    width: 100%;
    height: calc(100vh - 60px);
  }
}
</style>



