<template>
  <div class="article">

    <section class="left">
      <transition-group tag="ul" name="fade">
        <li v-for="(item, i) in list.list" :key="i">
          <div class="left" @click="route(item)">
            <h4>{{ item.title }}</h4>
            <p class="notes">
              <span class="category">{{ item.categoryName }}</span>
              <span class="time">{{ item.create_at | dateFormat('yyyy-MM-dd hh:mm') }}</span>
              <span class="view"><i class="el-icon-view"></i>{{ item.meta.views }}</span>
              <!-- <span>{{ item.meta.comments }}</span> -->
            </p>

          </div>

          <div class="right">
            <img :src="item.thumb || img" alt="">  
          </div>     
        </li>
      </transition-group>

      <el-pagination
        v-show="list.list && list.list.length > 0"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page.sync="pages.pageNo"
        :page-sizes="[10, 20, 30, 50]"
        :page-size="100"
        layout="prev, pager, sizes, next, jumper"
        :total="list.totalCount">
      </el-pagination>
    </section>

    <section class="right"></section>

  </div>
</template>

<script>
import img from '../assets/images/Logo1.png'
export default {
  name: 'articles',
  data () {
    return {
      img,
      pages: {
        pageNo: 1,
        pageSize: 10,
        keyword: ''
      }
    }
  },
  computed: {
    list () {
      return this.$store.state.article.list
    },
    label () {
      return this.$store.state.article.label
    }
  },
  methods: {
    getList () {
      this.$store.dispatch('article/getArtList', {
        ...this.pages
      })
    },
    getLabel () {
      this.$store.dispatch('article/getLabel')
    },
    handleSizeChange (val) {
      this.pages.pageNo = 1
      this.pages.pageSize = val
      this.getList()
    },
    handleCurrentChange (val) {
      this.pages.pageNo = val
      this.getList()
    },
    route (item) {
      this.$router.push('/show')
    }
  },
  created () {
    this.getList()
  }
}
</script>

<style lang="scss" scoped>
@import '../assets/scss/variable.scss';

.article {
  display: flex;
  padding: $md-pad 0;

  > .right {
    margin-left: $md-pad;
    width: 350px;
    background: $white;
  }

  > .left {
    flex-grow: 1;
    background: $white;
  }

  ul {
    padding: 0 20px;

    li {
      display: flex;
      cursor: pointer;

      .left {
        flex-grow: 1;
        padding: 10px 0;
        border-bottom: 1px dashed $border-color;

        > h4 {
          height: 64px;
          line-height: 32px;
        }

        > .notes {
          font-size: 12px;

          > span {
            margin-right: 20px;

            > i {
              margin-right: 6px;
            }
          }
        }
      }

      &:last-child {
        border: none;
      }

      .right {
        width: 100px;
        height: 100px;
      }
    }
  }

  @media screen and (max-width: 500px) {
    .right {
      display: none;
    }
  }

  .el-pagination {
    padding: 20px;
    text-align: right;
  }
}
</style>



